.menu-btn {
    position: absolute;
    display: flex;
    margin: 10px;
    float: left;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
    border-radius: 100vw;
    margin-left: 30px;
    z-index: 1000;
}

.menu-btn .menu-burger {
    width: 25px;
    height: 3px;
    background-color: white;
    border-radius: 3px;
    transition: all 0.5s ease-in-out;
}

.menu-btn:hover {
    background-color: rgba(255, 255, 255, 0.274);
}

.menu-btn .menu-burger::before,
.menu-btn .menu-burger::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 3px;
    background-color: white;
    border-radius: 3px;
    transition: all 0.5s ease-in-out;
}

.menu-btn .menu-burger::before {
    transform: translateY(-8px);
}

.menu-btn .menu-burger::after {
    transform: translateY(8px);
}

/* Animation */
.menu-btn.open .menu-burger {
    transform: translateX(-50px);
    background: transparent;
}

.menu-btn.open .menu-burger::before {
    transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-burger::after {
    transform: rotate(-45deg) translate(35px, 35px);
}
